'use strict';

// MODULES //

import isObject from '@stdlib/assert-is-object';
import isFunction from '@stdlib/assert-is-function';
import hasOwnProp from '@stdlib/assert-has-own-property';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Tests whether all own elements in a collection pass a test implemented by a predicate function.
*
* @param {Object} obj - input object
* @param {Function} predicate - test function
* @param {*} [thisArg] - execution context
* @throws {TypeError} first argument must be an object
* @throws {TypeError} second argument must be a function
* @returns {boolean} boolean indicating whether all own elements pass a test
*
* @example
* function isPositive( v ) {
*     return ( v > 0 );
* }
*
* var obj = {
*     'a': 1,
*     'b': 2,
*     'c': 3
* };
*
* var bool = everyOwnBy( obj, isPositive );
* // returns true
*/
function everyOwnBy( obj, predicate, thisArg ) {
	var key;
	if ( !isObject( obj ) ) {
		throw new TypeError( format( 'null3L', obj ) );
	}
	if ( !isFunction( predicate ) ) {
		throw new TypeError( format( 'null2H', predicate ) );
	}

	for ( key in obj ) {
		if ( hasOwnProp( obj, key ) ) {
			if ( !predicate.call( thisArg, obj[ key ], key, obj ) ) {
				return false;
			}
		}
	}
	return true;
}


// EXPORTS //

export default everyOwnBy;
